import React, { Component } from 'react'
import {Route, Switch, Redirect} from 'react-router-dom'
import MainBtn from './MainBtn'
import Animals from './Animals'
import Vegi from './Vegi'
import Nature from './Nature'
import Fruit from './Fruit'
import Toy from './Toy'
import RouterAnimal from './RouterAnimal'
import RouterFruit from './RouterFruit'
import RouterVegi from './RouterVegi'
import RouterNature from './RouterNature'
import RouterToy from './RouterToy'
import './App.css'

class HtmlTemplate extends Component {
    render() {
        return (
            <div className='App'>
                <div className='header'>
                    <div className='header-wrapper'>
                    <div className='logo'>K-E</div>
                    <div className='head-title'><h1>Kids-Encyclopedia</h1></div>
                    <div className='nav-items'>
                        <ul>
                            <MainBtn toPath="" BtnName="Animal"/>
                            <MainBtn toPath="fruit" BtnName="Fruit"/>
                            <MainBtn toPath="vegi" BtnName="Vegi"/>
                            <MainBtn toPath="nature" BtnName="Nature"/>
                            <MainBtn toPath="toy" BtnName="Toy"/>
                       </ul>
                    </div>
                    </div>
                </div>
                <div className='btn-area'>                   
                        <Switch>
                            <Route  path="/vegi" component={Vegi}></Route>
                            <Route  path="/fruit" component={Fruit}></Route>
                            <Route  path="/nature" component={Nature}></Route>
                            <Route  path="/toy" component={Toy}></Route>                           
                            <Route  path="/" component={Animals}></Route>
                            <Redirect to="/" />
                        </Switch>                                       
                </div>
                <RouterAnimal/>
                <RouterFruit/>
                <RouterVegi/>
                <RouterNature/>
                <RouterToy/>
                <div className='footer'>
                    <p className='unplash'>*Images Credit: Unplash</p>
                    <p>&copy;Sharif Hossain 2020</p>
                </div>
            </div>
        )
    }
}
export default HtmlTemplate;
